<script>
import firebase from 'firebase'
import axios from 'axios'
let db = firebase.firestore()
export default{
    data(){
        return{
            vdata:{
              title:"GOFBUY NOTIFIKASI"
            }
        }
    },
    methods: {
        notif(){
            axios.post('https://fcm.googleapis.com/fcm/send',{
                                data: {
                                title: this.vdata.title,
                                text: "isi notifiactiaon",
                                body: this.vdata.text,
                                android_channel_id:"alarm",
                                sound: "true",
                                high_priority: "high",
                                ledColor: [255, 255, 128, 16],
                                    vibrationPattern: [250, 1000, 250, 500],
                                show_in_foreground: true,
                                    vibrate: [100, 50, 200]
                                },
                                priority: "high",
                                condition:"'gofbuy' in topics || 'industry-tech' in topics"
                                },{
                                    headers:{
                                        "Authorization":"key=AAAAm7m-SgM:APA91bFNeEP0bPIoqhRNhH3X7xSaaFc8iG0h2JLm3vu8V3-rbqDA58b2WzRZWRHW0zD1FKoSOgBSwyolU4s-EKLM6sLNU9_kzJ9BCIaE-3ZQWNXfzqYQPBZ8bdFXmDkxTs4WMezHrmP0",
                                        "Content-Type":"application/json"
                                    }
                                }
                        ).then(res=>{

                        })
        },
        set(){
          if(localStorage.getItem('notifid')){
              db.collection('notif').doc('BaOUtzlh9mRtoe9dSv2f').set({fcm:localStorage.getItem('notifid')},{merge:true}).then(res=>{
                alert("Berhasil di simpan owner device id !")
            })
            }else{
              alert("Tidak ada notif id terdaftar di device")
            }
        }
    },
}
</script>